/* App.css */
.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  padding: 20px;
  box-sizing: border-box;
}

header {
  margin-top: 20px;
  margin-bottom: 20px;
  text-align: center;

}

header h1 {
  font-family: "Open Sans", sans-serif;
  font-size: 36px;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #000;
  margin: 0;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

label {
  margin-bottom: 50px;
  font-size: 18px;
}

.button-group {
  display: flex;
  gap: 10px;
}

.btn, .input, .field {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  letter-spacing: 2px;
  text-decoration: none;
  text-transform: uppercase;
  color: #000;
  cursor: pointer;
  border: 3px solid;
  padding: 0.25em 0.5em;
  box-shadow: 1px 1px 0px 0px, 2px 2px 0px 0px, 3px 3px 0px 0px, 4px 4px 0px 0px, 5px 5px 0px 0px;
  position: relative;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}
.field {
  font-weight: bold;
  margin-top: 20px;
}

input[type="number"] {
  -moz-appearance: textfield;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.label {
  display: flex;
  gap: 10px;
  padding-top: 20px;
}
.input:focus {
  outline: none;
  background-color: transparent; /* Or you can specify any other color */
}

.btn:active {
  box-shadow: 0px 0px 0px 0px;
  top: 5px;
  left: 5px;
}

@media (min-width: 768px) {
  .btn, .input {
    padding: 0.25em 0.75em;
  }
}

.placeholder-text {
  text-align: center;
  margin-top: 20px;
}

.placeholder-text h2 {
  font-family: "Open Sans", sans-serif;
  font-size: 24px;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #000;
  margin-bottom: 10px;
}

.placeholder-text p {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  color: #000;
  line-height: 1.5;
}
.gif {
  align-items: center;
  justify-content: center;
  width: 80%;
  height: 80%;
  object-fit: contain;
}
.gif-container {
  display: flex;
  align-items: center;
  justify-content: center;
}
.connect-wallet-container{
  padding-bottom:20px ;
}
.minimumbet{
  padding-top:20px ;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
}